<template>
    <div class="feedback">
        <navigate title="意见反馈" position="absolute"/>
        <div style="height: 10px; background-color: #efefef;"></div>
        <div class="scroll-wrapper">
            <div class="feedback-box">
                <div class="field-wrapper">
                    <mu-text-field v-model="params.content" :rows="5" :rows-max="5"
                                   placeholder="您的意见将帮助我们改善产品和服务"
                                   full-width
                                   multi-line
                                   solo/>
                </div>
                <div class="picture-wrapper" :class="{empty: !image}"
                     :style="{backgroundImage: `url(${params.imgPath || image})`}"
                     @click="handlerAddImage">
                </div>
            </div>
            <div style="height: 15px; background-color: #efefef"></div>
            <div style="padding: 0 15px; background-color: #fff;">
                <mu-text-field v-model="params.contact" placeholder="请输入您的联系方式" full-width solo/>
            </div>
            <div style="height: 15px; background-color: #efefef"></div>
            <div style="padding: 0 15px 50px;">
                <mu-button style="width: 100%; height: 45px; color: #fff; background-color: #2196f3;"
                           :disabled="loading"
                           v-loading="loading"
                           data-mu-loading-size="24"
                           @click="submit"
                           flat>提交
                </mu-button>
            </div>


            <div style="visibility: hidden">
                <input type="file" accept="image/*" ref="file" @change="handlerPictureChange">
            </div>
        </div>
    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import {userMixins} from "../../../common/mixins"
    import {saveOrUpdateFeedback} from "../../../api/personal"
    import {upload} from "../../../api/sys"

    export default {
        name: "feedback",
        mixins: [userMixins],
        data() {
            return {
                loading: false,
                params: {
                    content: '',
                    contact: '',
                    imgPath: '',
                    token: '',
                },
                image: ''
            }
        },
        activated() {
            this.params = {
                content: '',
                contact: this.user.phone || '',
                imgPath: '',
                token: ''
            }

            this.image = ''
        },
        methods: {
            // 提交
            async submit() {
                this.loading = true

                this.$toast({
                    message: '正在提交 ...',
                    duration: 0
                })

                if (!this.params.content && !this.image) {
                    this.loading = false
                    return this.$toast('请填写您的反馈意见')
                }

                if (this.image) {
                    this.params.imgPath = await upload(this.$refs.file.files[0])
                }


                saveOrUpdateFeedback(this.params).then(res => {
                    this.loading = false

                    this.$toast('提交成功!')

                    this.$router.back()
                }).catch(err => {
                    this.$toast('提交失败')
                })
            },
            // 更换图片
            handlerPictureChange() {
                const image = this.$refs.file.files[0]

                this.image = URL.createObjectURL(image)
            },
            // 添加图片
            handlerAddImage() {
                this.$refs.file.click()
            }
        },
        components: {
            Navigate
        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../../common/stylus/variable.styl"

    .feedback
        padding-top 46px
        background-color #efefef
        .feedback-box
            display flex
            align-items flex-start
            padding 10px 15px
            background-color #fff
            .field-wrapper
                flex 1
                padding-right 15px
            .picture-wrapper
                position relative
                width 58px
                min-height 58px
                max-height 136px
                border 1px solid #f2f2f2
                background-repeat no-repeat
                background-position center center
                background-size cover
                overflow hidden
                &.empty:after
                    content '上传图片'
                    position absolute
                    left 0
                    top 0
                    width 58px
                    height 58px
                    padding-top 36px
                    line-height 1.5
                    text-align center
                    font-size 10px
                    color #fff
                    background #efefef url("../../../../static/icon/personal_center__upload_photos.png") no-repeat center 13px
                    background-size 22px
</style>



